<template>
    <div class="body" >
        <div class="fixation">
            <div class="input_div">
                <div class="input_name">真实地址</div>
                <input  placeholder="EP:C://person-api"  v-model="model.realPath" class="input"/>
            </div>
            <div class="input_div">
                <div class="input_name">实体名称</div>
                <input  placeholder="EP:Person"  v-model="model.entityName" class="input"/>
            </div>
            <div class="input_div">
                <div class="input_name">实体包名</div>
                <input  placeholder="EP:person"  v-model="model.entityPackageName" class="input"/>
            </div>
            <div class="input_div">
                <div class="input_name">项目包名</div>
                <input   placeholder="EP:com.note" v-model="model.projectPackageName" class="input"/>
            </div>
            <div class="input_div">
                <div class="input_name">数据表名</div>
                <input   placeholder="EP:jf_person" v-model="model.tableName" class="input"/>
            </div>
            <div  class="input_div">
                <div class="input_name">强制生成</div>
                <select  v-model="model.compelFlag" class="input">
                    <option value=false>否</option>
                    <option value=true>是</option>
                </select>
            </div>
        </div>
        <button @click="start" style="height: 30px;" class="button_a hvr-buzz-out">开始代码生成</button>
        <button v-show="fieldIndexFlag" style="height: 30px;" @click="add()" class="button_a hvr-buzz-out">增加新字段</button>
        <div  v-for="attr in entityAttributes">
            <div class="field">
                <div class="f_header">字段{{entityAttributes.indexOf(attr)+1}}</div>
                <div class="f_body">
                    <div class="field_attr">
                        <div style="height: 10px"></div>
                        <div class="f_div">
                            <div class="f_name">字段名称</div>
                            <input  placeholder="EP:name" v-model="attr.fieldName" class="f_input">
                        </div>
                    </div>
                    <div class="field_attr">
                        <div style="height: 10px"></div>
                        <div  class="f_div">
                            <div class="f_name">字段备注</div>
                            <input v-model="attr.describe" placeholder="EP:姓名" class="f_input">
                        </div>
                    </div>
                    <div class="field_attr">
                        <div style="height: 10px"></div>
                        <div class="f_div">
                            <div class="f_name">注解标识</div>
                            <select v-model="attr.annotationFlag" class="f_select">
                                <option value="false">否</option>
                                <option value="true">是</option>
                            </select>
                        </div>
                    </div>
                    <div class="field_attr">
                        <div style="height: 10px"></div>
                        <div class="f_div">
                            <div class="f_name">创建标识</div>
                            <select v-model="attr.createFlag" class="f_select">
                                <option value="true">是</option>
                                <option value="false">否</option>
                            </select>
                        </div>
                    </div>

                    <div class="field_attr">
                        <div style="height: 10px"></div>
                        <div class="f_div">
                            <div class="f_name">修改标识</div>
                            <select  v-model="attr.updateFlag"  class="f_select">
                                <option value="true">是</option>
                                <option value="false">否</option>
                            </select>
                        </div>
                    </div>
                    <div class="field_attr">
                        <div style="height: 10px"></div>
                        <div class="f_div">
                            <div class="f_name">字段类型</div>
                            <input  v-model="attr.fieldType" :value="attr.selectType" class="f_input_auto"/>
                            <select   @change="selectType(attr)"   v-model="attr.selectType" class="f_select_auto">
                                    <option   v-for="item in defaultFieldTypes"    :value="item">{{item}}</option>
                                    <option   value="Creation">自定义</option>
                            </select>
                            <br>
                        </div>
                    </div>
                    <div v-if="attr.autoInputFlag" class="field_attr">
                        <div style="height: 10px"></div>
                        <div class="f_div">
                            <div class="f_name">自定义包</div>
                            <input  v-model="attr.packageName" placeholder="EP:com.note.entity.*" class="f_input">
                        </div>
                    </div>
                </div>
                <div class="f_footer">
                    <button @click="deleteAttr(attr)" class="button_d hvr-buzz-out">删除该字段</button>
                    <div style="flex: 1">
                    </div>
                    <button @click="add()" class="button_a hvr-buzz-out">增加新字段</button>
                </div>
            </div>
        </div>
    </div>
</template>



<script>

    export default {
        data() {
            return {
                selected: 'String',
                fieldIndexFlag:false,
                defaultFieldTypes:["String","Integer","Date","Double","Float","Boolean","Long","Short","Byte"],
                model:{compelFlag:false},
                entityAttributes:[{"autoInputFlag":false,"selectType":"String",annotationFlag:false,createFlag:true,updateFlag:false,fieldType:"String"}]
            }
        },
        methods:{
            add(){
                this.entityAttributes.push({"autoInputFlag":false,"selectType":"String",annotationFlag:false,createFlag:true,updateFlag:false,fieldType:"String"});
                this.fieldIndexFlag=false
            },
            deleteAttr(attr){
                if(confirm("你确定要删除字段："+(!attr.fieldName?"‘未填字段名’":attr.fieldName))) {
                    if( this.entityAttributes.length==1){
                        let index = this.entityAttributes.indexOf(attr);
                        this.entityAttributes.splice(index, 1);
                        this.fieldIndexFlag=true;
                    }
                    let index = this.entityAttributes.indexOf(attr);
                    this.entityAttributes.splice(index, 1);
                }
            },
            selectType(attr){
                console.log(attr.selectType);
                if(attr.selectType=="Creation"){
                    attr.autoInputFlag = true;
                }else{
                    attr.fieldType=attr.selectType;
                    attr.autoInputFlag = false;
                }
            },
            start(){
                this.model.entityAttributes=this.entityAttributes;
                this.$http.post("/automatic/create",this.model,{}).then(
                        function (res) {
                          alert("代码生成成功！");
                        },
                        function(res){
                            switch(res.status)
                            {
                                case 400:
                                    alert("输入不完整！");
                                    break;
                                case 500:
                                    alert("服务器异常！");
                                    break;
                                default:
                                    alert("未知code："+res.status);
                                  break;
                            }
                        }

                );
            }

        }
    }
</script>

<style scoped>
    @import "../css/automatic.css";
</style>



